.PokemonGrid {
    display: flex;
    flex-flow: column;
    height: 570px;
    background-color: rgb(219, 219, 219);
    border-radius: 50px;
    
}

.score_container {
    display: flex;
    flex-flow: column;
    align-items: center;
    background-color: rebeccapurple;
    padding: 10px;
    border-radius: 50px 50px 0 0;
}

.current_score_container {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 20px;
    color: #ffff00;
}

.current_score_container > p {
    font-size: 24px;
}

.yourscore {
    margin-left: 10px;
    font-size: 48px;
}

.pkmngrid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto;
    justify-items: center;
    grid-gap: 5px;
    align-items: center;
    width: 100%;
    height: 570px;
    background-color: rgb(219, 219, 219);
    border-radius: 10px;
}

