.Start {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 570px;
  background-color: steelblue;
  border-radius: 10px;
  z-index: 5;

}
.pokelogo {
    width: 40%;
    height: 40%;
    margin-top: 30px;
}

.Start h1 {
    font-size: 40px;
    margin-top: -20px;
    margin-bottom: 60px;
    color: rgb(235, 235, 1);
    text-shadow: 0px 3px #3b45ca;
}

.Start p {
    margin-bottom: 20px;
    color: rgb(255, 255, 255);
}

.btn_start {
    width: 200px;
    background-color: hsl(120, 80%, 53%);
}